@import url("https://fonts.googleapis.com/css2?family=Stint+Ultra+Condensed&display=swap");

@import url("https://fonts.googleapis.com/css2?family=Stint+Ultra+Condensed&display=swap");

@import url("https://fonts.googleapis.com/css2?family=PT+Sans+Narrow&family=Stint+Ultra+Condensed&display=swap");

* {
  box-sizing: border-box;
  margin: 0px;
  padding: 0px;
  --navcolor: #e74c3c;
}
/* font-family: 'Oswald', sans-serif;
 font-family: 'Roboto', sans-serif;
 font-family: 'Stint Ultra Condensed', cursive; */
body {
  margin-right: 0px;
  overflow-x: hidden;
}
.navdiv {
  background: var(--navcolor);
  padding: 1rem;
}
.navdiv ul li {
  margin-left: 2rem;
}

.nav {
  margin-right: 10rem;
}
.navdiv ul li a {
  font-size: 1.2rem;
  font-weight: 500;
  color: white;
  font-family: roboto;
}
.navdiv ul li:hover {
  background: white;
  color: #c43c43;
  border-radius: 1rem;
}
.navdiv ul a:hover {
  color: var(--navcolor);
}
.nav-item .dropdown-menu a {
  color: var(--navcolor);
}
.navdiv ul h1 {
  font-family: "Oswald", sans-serif;
  font-size: 1.7rem;
  color: skyblue;
  font-weight: bolder;
}
.icon {
  color: skyblue;
  font-size: 2.5rem;
  font-weight: bolder;
}
.logo-div {
  position: absolute;
  display: flex;
  flex-direction: row;
  left: 10rem;
}
.greeting-div {
  display: flex;
  flex-direction: row;
  background: #fff;
  width: 100%;
}
.img-div img {
  height: 40rem;
  width: 45rem;
  border: none;
  outline: none;
}
.text-center {
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.text-center h1 {
  font-size: 6rem;
  font-family: "Oswald", sans-serif;
  color: var(--navcolor);
}
.text-center h4 {
  color: black;
}
.text-center button {
  background: var(--navcolor);
  width: 9rem;
  height: 4rem;
  color: white;
  border: none;
  outline: none;
  border-radius: 3rem;
  margin: 1rem 0 0 27rem;
  position: relative;
  overflow: hidden;
  transition: all 0.5s linear;
  z-index: 1;
  font-weight: bold;
  font-size: 1.4rem;
}

.container .chief {
  display: inline-block;
  margin-left: 25rem;
}

.text-center button::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  background-color: rgb(211, 173, 173);
  transform: translate(100%);
  transition: all 0.5s linear;
  width: 100%;
  height: 100%;
}
.text-center button:hover:before {
  transform: translate(0);
  z-index: -1;
}
.text-center button:hover {
  color: #fff;
}
.main {
  background: #d4d1ce;
}
.info-div h6 {
  font-size: 1.3rem;
}
.main h1 {
  color: var(--navcolor);

  font-weight: bolder;
  text-align: left;
  display: inline-block;
  border-bottom: 0.2rem solid black;
  font-family: "Roboto", sans-serif;
  font-size: 3rem;
  word-spacing: 1rem;
}
.main h3 {
  color: #e4ad14;
  font-family: "Stint Ultra Condensed", cursive;
  font-weight: bold;
  font-size: 2rem;
  word-spacing: 0.2rem;
}
.main p {
  color: #444a4e;
  text-align: left;
  line-height: 1.8rem;

  font-family: roboto;
}
.justify {
  margin-top: 7rem;
}
.sign-div {
  display: flex;
  justify-content: row;
}
.right-sign {
  background: var(--navcolor);
  color: white;
  border-radius: 50%;
}
.sign-div p {
  display: inline-block;
  margin-left: 0.5rem;
}

.div-img {
  background: transparent;
  justify-content: center;
  border-color: #d4d1ce;
}
.div-img img {
  display: flex;
  justify-content: center;
  margin-top: 12rem;
  width: 90%;
  margin-right: 2rem;
  background: #d4d1ce;
}
.second {
  background: #fff;
}
.cdiv img {
  width: 15rem;
  height: 15rem;
  background: rgb(85, 77, 77);
  margin-top: 5rem;
}
/* .second{
    display: flex;
    flex-direction: column;
    justify-content: center;
} */
.size .fnt {
  line-height: 2rem;
}
.cdiv h1 {
  border: none;
  font-size: 3rem;
  font-family: "Oswald", sans-serif;
}
.cdiv {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.containerm {
  background: wheat;
}
.card {
  border-radius: 2rem;
  margin-bottom: 3rem;
}
.container h1 {
  font-family: "PT Sans Narrow", sans-serif;
  color: var(--navcolor) !important;
  font-weight: bold;
  display: inline-block;
  margin-top: 2rem;
}

.card {
  transition: all 0.2s linear;
  box-shadow: 0.1rem 0.1rem 0.1rem 0.1rem gray;
  width: 18rem;
}
.card:hover {
  transform: scale(1.05);
}
.social {
  color: var(--navcolor) !important;
  border-radius: 2rem;
  transform: scale(1.4);
}
.btn-div {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  margin-top: 5rem;
}
.btn-div button {
  margin-left: 7rem;
  border: none;
  outline: none;
}
.btn-icon {
  transform: scale(2.5);
}

.menu-item {
  display: flex;
  flex-direction: row;
}
.info-div {
  margin-left: 3rem;
}
.div-btn-1 {
  display: flex;
  flex-direction: column;
}
.div-contact {
  display: flex;
  flex-direction: row;
}
.div1 {
  display: flex;
}
.div2 {
  margin-left: 0.9rem;
}
.kochu h2 {
  color: white;
}
::placeholder {
  color: var(--navcolor);
  display: inline-block;
}
.last-in .input {
  height: 2rem;
  padding: 1.5rem;
}
